export function stringHash(str) {
    var hash = 0;
    var data = String(str);
    if (data.length === 0)
        return hash;
    for (var i = 0; i < data.length;) {
        var char = data.charCodeAt(i);
        hash = (hash << 5) - hash + char; // eslint-disable-line no-bitwise
        hash &= hash; // eslint-disable-line no-bitwise
        i += 1;
    }
    return hash;
}
