import React from 'react';

//legao
import { Select,InputNumber,Drawer,Card,Tooltip } from '@feb-team/legao-react';
import '@feb-team/legao-react/dist/styles/css/legao.all.css';

class Mag extends React.PureComponent{
	constructor(props) {
		super(props);
		this.state = {
			visible: false,
			header:'  1',
			Element:'',
			Race:'',
			Type:'',
			Amplifier:'1',
		}
	}
	to_skill(){
		let Element=this.state.Element;
		let Race=this.state.Race;
		let Type=this.state.Type;
		let Amplifier=this.state.Amplifier;
		switch (Element) {
			case '':Element="*"; break;
			case '':Element="w"; break;
			case '':Element="f"; break;
			case '':Element="t"; break;
			case '':Element="l"; break;
			case '':Element="d"; break;
			default:
		}
		switch (Race) {
			case '':Race="*"; break;
			case '':Race="G"; break;
			case '':Race="E"; break;
			case '':Race="H"; break;
			case '':Race="A"; break;
			case '':Race="D"; break;
			case '':Race="S"; break;
			case '':Race="M"; break;
			default:
		}
		switch (Type) {
			case '':Type="A"; break;
			case '':Type="H"; break;
			case '':Type="R"; break;
			default:
		}
		return `mag=${Element},${Race},${Type},${Amplifier};`
	}
	header(){
		if(this.state.Element==="")
			return this.state.Race+" "+this.state.Type+" "+this.state.Amplifier+"";
		else if(this.state.Race==="")
			return this.state.Element+" "+this.state.Type+" "+this.state.Amplifier+"";
		else	
			return this.state.Element+" "+this.state.Race+" "+this.state.Type+" "+this.state.Amplifier+"\n";
	}
	render() {
		this.props.self.skill=this.to_skill();
		return (
			<React.Fragment>
			<Card shadow={'always'} onClick={()=>{
				this.setState({	visible: true})
            }}>
				{this.state.header.split('\n').map(i=><p>{i}</p>)}
			</Card>
            <Drawer 
				visible={this.state.visible}
				className="test"
				onClose={()=>{this.setState({visible: false})}}
				close={false}
				width={320}
				height={320}
				placement={"right"}
				title={<p>{this.state.header}</p>}
				mask={true}
				maskClosable={true}
				zIndex={0}
				afterVisibleChange={()=> {}}
            >
				<Tooltip title="" placement={"Left"}>
						<div>
							<Select defaultValue={this.state.Element} clear={false} placeholder={""} 
							onChange={(Element)=>{
								this.setState({Element},()=>{
									this.setState({header:this.header()});
								});
							}}>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
							</Select>
						</div>
				</Tooltip>
				<Tooltip title="" placement={"Left"}>
						<div>
						<Select defaultValue={this.state.Race} clear={false} placeholder={""} 
							onChange={(Race)=>{
								this.setState({Race},()=>{
									this.setState({header:this.header()});
								});
							}}>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
							</Select>
						</div>
				</Tooltip>
				<Tooltip title="" placement={"Left"}>
						<div>
							<Select defaultValue={this.state.Type} clear={false} placeholder={""} 
							onChange={(Type)=>{
								this.setState({Type},()=>{
									this.setState({header:this.header()});
								});
							}}>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
							</Select>
						</div>
				</Tooltip>
				<Tooltip title="" placement={"Left"}>
						<div>
							<InputNumber defaultValue={this.state.Amplifier} max={9223372036854775807} min={0} decimalPlaces={3} step={0.1} style={{width:'200px'}} 
							onChange={(Amplifier)=>{
								if(isNaN(Amplifier))
									Amplifier=0;
								this.setState({Amplifier},()=>{
									this.setState({header:this.header()});
								});
							} } 
						/>
						</div>
				</Tooltip>
            </Drawer>
		</React.Fragment>
		)
	}
}
export default Mag;