import React from 'react';

//legao
import { Button,Select,Input,Drawer,message } from '@feb-team/legao-react';
import '@feb-team/legao-react/dist/styles/css/legao.all.css';


class Condition extends React.Component {

	constructor(props) {
		super(props);
		this.state = {
			Element:"",
			Race:"",
			Series:"",
		}
	}
	deleteItems(props){
		console.log(props)
		const Index = props.target.state.items.findIndex((i)=>i.id===props.id);
		props.target.deleteItems(props.target,Index);
	}
	render(){
		if(this.props.type===1)
			return(
				<div style={{ position: 'relative '} }>
					<Select style={{width:'100px'}} defaultValue={this.state.Element} clear={false} placeholder={""} onChange={(value)=>{this.setState({Element:value})} }>
						<Select.Option value={""}></Select.Option>
						<Select.Option value={""}></Select.Option>
						<Select.Option value={""}></Select.Option>
						<Select.Option value={""}></Select.Option>
						<Select.Option value={""}></Select.Option>
						<Select.Option value={""}></Select.Option>
					</Select>
					<Select style={{width:'100px'}} defaultValue={this.state.Race} clear={false} placeholder={""} onChange={(value)=>{this.setState({Race:value})} }>
						<Select.Option value={""}></Select.Option>
						<Select.Option value={""}></Select.Option>
						<Select.Option value={""}></Select.Option>
						<Select.Option value={""}></Select.Option>
						<Select.Option value={""}></Select.Option>
						<Select.Option value={""}></Select.Option>
						<Select.Option value={""}></Select.Option>
						<Select.Option value={""}></Select.Option>
					</Select>
					<Button onClick={()=>this.deleteItems(this.props)} style={{ position: 'absolute',top:"0px",right:"0px"} }></Button>
				</div>
			);
		else
			return(
				<div style={{ position: 'relative '} }>
					<Input placeholder="" onChange={(value)=>{this.setState({Series:value})} } />
					<Button onClick={()=>this.deleteItems(this.props)} style={{ position: 'absolute',top:"0px",right:"0px"} }></Button>
				</div>
			);
	}
}

class CardCondition extends React.PureComponent {
	state = {
		visible: false,
		items: []
	}
	deleteItems(target,position){
		target.state.items.splice(position,1);
		target.setState({items:this.state.items.concat([])});
	}
	render() {
		return (
		<React.Fragment>
			<Button type="info" onClick={()=>{
				this.setState({
					visible: true
				})
            }}></Button>
            <Drawer 
				visible={this.state.visible}
				className="test"
				onClose={()=>this.setState({visible: false})}
				close={true}
				width={320}
				height={320}
				placement={"right"}
				title={<p> / </p>}
				mask={true}
				maskClosable={true}
				zIndex={2000}
				afterVisibleChange={()=> {}}
            >
				<Button onClick={()=>{
					if(this.state.items.length<15){
						this.state.items.push({type:1,id:Date.now()})
						this.setState({items:this.state.items.concat([])});
					}
					else
						message.error('15',3000)
				}}>/</Button>
				<Button onClick={()=>{
					if(this.state.items.length<15){
						this.state.items.push({type:2,id:Date.now()})
						this.setState({items:this.state.items.concat([])});
					}
					else
						message.error('15',3000)
				}}></Button>
				<div>
					{this.state.items.map(i => <Condition type={i.type} id={i.id} target={this}/>)}
				</div>
            </Drawer>
		</React.Fragment>
		)
	}
}
export default CardCondition;