import React from 'react';

//legao
import { Select,InputNumber,Drawer,Card,Tooltip } from '@feb-team/legao-react';
import '@feb-team/legao-react/dist/styles/css/legao.all.css';

class Gsbc extends React.PureComponent {
	constructor(props) {
		super(props);
		this.state = {
			visible: false,
			header:' 1 \n 1 ',
			Count:1,
			Type:'',
			Drop:1,
			Element:'',
			set_skill:props.set_skill,
			self:props.self,
		}
	}
	to_skill(){
		let Count=this.state.Count;
		let Type=this.state.Type;
		let Drop=this.state.Drop;
		let Element=this.state.Element;
		switch (Type) {
			case '':Type="RDM"; break;
			case '':Type="SLF"; break;
			default:
		}
		switch (Element) {
			case '':Element="0"; break;
			case '':Element="1"; break;
			case '':Element="2"; break;
			case '':Element="3"; break;
			case '':Element="4"; break;
			case '':Element="5"; break;
			case '':Element="6"; break;
			case '':Element="7"; break;
			case '':Element="8"; break;
			case '':Element="9"; break;
			case '':Element="10"; break;
			case '':Element="11"; break;
			default:
		}
		return `gsbc=${Count},${Type},${Drop},${Element},-1;`
	}
	header(){
		return ` ${this.state.Count} \n${this.state.Type===""?"":""} ${this.state.Drop} ${this.state.Element}`;
	}
	render() {
		this.state.set_skill(this.state.self,this.to_skill());
		return (
				<React.Fragment>
				<Card shadow={'always'} onClick={()=>{
					this.setState({	visible: true})
				}}>
					{this.state.header.split('\n').map(i=><p>{i}</p>)}
				</Card>
				<Drawer 
					visible={this.state.visible}
					className="test"
					onClose={()=>this.setState({visible: false})}
					close={true}
					width={320}
					height={320}
					placement={"right"}
					title={<p>{this.state.header}</p>}
					mask={true}
					maskClosable={true}
					zIndex={0}
					getContainer={null}
					afterVisibleChange={()=> {}}
            	>
					<Tooltip title="" placement={"Left"}>
						<div>
							<InputNumber style={{width:'200px'}} defaultValue={this.state.Count} max={9223372036854775807} min={0} decimalPlaces={3} step={1} 
							onChange={(Count)=>{
									if(isNaN(Count))
										Count=0;
									this.setState({Count},()=>{
										this.setState({header:this.header()});
									});
								} } 
							/>
						</div>
					</Tooltip>
					<Tooltip title="" placement={"Left"}>
						<div>
							<Select defaultValue={this.state.Type} clear={false} placeholder={""} 
							onChange={(Type)=>{
								this.setState({Type},()=>{
									this.setState({header:this.header()});
								});
							}}>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
							</Select>
						</div>
					</Tooltip>
					<Tooltip title="" placement={"Left"}>
						<div>
							<InputNumber style={{width:'200px'}} defaultValue={this.state.Drop} max={9223372036854775807} min={0} decimalPlaces={3} step={1}
								onChange={(Drop)=>{
									if(isNaN(Drop))
										Drop=0;
									this.setState({Drop},()=>{
										this.setState({header:this.header()});
									});
								} } 
							/>
						</div>
					</Tooltip>
					<Tooltip title="" placement={"Left"}>
						<div>
							<Select defaultValue={this.state.Element} clear={false} placeholder={""} 
							onChange={(Element)=>{
								this.setState({Element},()=>{
									this.setState({header:this.header()});
								});
							}}>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
							</Select>
						</div>
					</Tooltip>				
			</Drawer>
		</React.Fragment>
		)
	}
}
export default Gsbc;