import React from 'react';

//legao
import { Select,InputNumber,Drawer,Card,Tooltip } from '@feb-team/legao-react';
import '@feb-team/legao-react/dist/styles/css/legao.all.css';

class DynaMag extends React.PureComponent {
	constructor(props) {
		super(props);
		this.state = {
			visible: false,
			header:' 1',
			Count:0,
			Type:'',
			Element:'',
			Race:'',
			Amplifier:1,
			set_skill:props.set_skill,
			self:props.self,
		}
	}
	to_skill(){
		let Count=this.state.Count;
		let Type=this.state.Type;
		let Element=this.state.Element;
		let Race=this.state.Race;
		let Amplifier=this.state.Amplifier;
		switch (Type) {
			case '':Type="-1"; break;
			case '':Type="0"; break;
			case '':Type="1"; break;
			case '':Type="2"; break;
			case '':Type="3"; break;
			case '':Type="4"; break;
			case '':Type="5"; break;
			default:
		}
		switch (Element) {
			case '':Element="-1"; break;
			case '':Element="0"; break;
			case '':Element="1"; break;
			case '':Element="2"; break;
			case '':Element="3"; break;
			case '':Element="4"; break;
			default:
		}
		switch (Race) {
			case '':Race="-1"; break;
			case '':Race="0"; break;
			case '':Race="1"; break;
			case '':Race="2"; break;
			case '':Race="3"; break;
			case '':Race="4"; break;
			case '':Race="5"; break;
			case '':Race="6"; break;
			default:
		}
		//dynaMag=1,0,-1,-1,5;
		return `dynaMag=${Count},${Type},${Element},${Race},${Amplifier};`
	}
	header(){
		let text=''
		if(this.state.Count>0)
			text+=''+this.state.Count+''+this.state.Type+'\n';
		if(this.state.Element==="")
			return text+this.state.Race+" "+this.state.Amplifier+"";
		else if(this.state.Race==="")
			return text+this.state.Element+" "+this.state.Amplifier+"";
		else	
			return text+this.state.Element+' '+this.state.Race+" "+this.state.Amplifier+"";
	}
	render() {
		this.state.set_skill(this.state.self,this.to_skill());
		return (
				<React.Fragment>
				<Card shadow={'always'} onClick={()=>{
					this.setState({	visible: true})
				}}>
					{this.state.header.split('\n').map(i=><p>{i}</p>)}
				</Card>
				<Drawer 
					visible={this.state.visible}
					className="test"
					onClose={()=>this.setState({visible: false})}
					close={true}
					width={320}
					height={320}
					placement={"right"}
					title={<p>{this.state.header}</p>}
					mask={true}
					maskClosable={true}
					zIndex={0}
					getContainer={null}
					afterVisibleChange={()=> {}}
            	>
					<Tooltip title="" placement={"Left"}>
						<div>
							<InputNumber style={{width:'200px'}} defaultValue={this.state.Count} max={9223372036854775807} min={0} decimalPlaces={3} step={1} 
							onChange={(Count)=>{
									if(isNaN(Count))
										Count=0;
									this.setState({Count},()=>{
										this.setState({header:this.header()});
									});
								} } 
							/>
						</div>
					</Tooltip>
					<Tooltip title="" placement={"Left"}>
						<div>
							<Select defaultValue={this.state.Type} clear={false} placeholder={""} 
							onChange={(Type)=>{
								this.setState({Type},()=>{
									this.setState({header:this.header()});
								});
							}}>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
							</Select>
						</div>
					</Tooltip>
					
					<Tooltip title="" placement={"Left"}>
						<div>
							<Select defaultValue={this.state.Element} clear={false} placeholder={""} 
							onChange={(Element)=>{
								this.setState({Element},()=>{
									this.setState({header:this.header()});
								});
							}}>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
							</Select>
						</div>
					</Tooltip>
					<Tooltip title="" placement={"Left"}>
						<div>
							<Select defaultValue={this.state.Race} clear={false} placeholder={""} 
							onChange={(Race)=>{
								this.setState({Race},()=>{
									this.setState({header:this.header()});
								});
							}}>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
							</Select>
						</div>
					</Tooltip>
					<Tooltip title="" placement={"Left"}>
						<div>
							<InputNumber style={{width:'200px'}} defaultValue={this.state.Amplifier} max={9223372036854775807} min={0} decimalPlaces={3} step={0.1}
								onChange={(Amplifier)=>{
									if(isNaN(Amplifier))
										Amplifier=0;
									this.setState({Amplifier},()=>{
										this.setState({header:this.header()});
									});
								} } 
							/>
						</div>
					</Tooltip>
			</Drawer>
		</React.Fragment>
		)
	}
}
export default DynaMag;