import React from 'react';

//legao
import { Checkbox,InputNumber,Drawer,Card,Tooltip } from '@feb-team/legao-react';
import '@feb-team/legao-react/dist/styles/css/legao.all.css';

class Dh extends React.PureComponent{
	constructor(props) {
		super(props);
		this.state = {
			visible: false,
			header: ' 0% ',
			Percentage: 0,//
			HPGT: 0,//HP
			AC: 0,//
			AI: [],//
			
		}
	}
	to_skill(){
		let Percentage=this.state.Percentage;
		let HPGT=this.state.HPGT;
		let AC=this.state.AC;
		let AI=this.state.AI;
		AI=AI.join("")
			 .replace("",0)
			 .replace("",1)
			 .replace("",2)
			 .replace("",3)
			 .replace("",4)
			 .replace("",5);
		return `dh=p=${Percentage},condiHpGt=${HPGT},condiAC=${AC},condiAI${AI};`
	}
	header(){
		let text='';
		if(this.state.HPGT>0)
			text+= ""+this.state.HPGT+"%";
		if(this.state.AC>0){
			if(text!=='')
				text+="\n";
			text+= ""+this.state.AC+"";
		}
		if(this.state.AI.length>0){
			if(text!=='')
				text+="\n";
			if(this.state.AI.length>1)
				text+=""
			text+= ""+this.state.AI.join("")+"";
		}
		if(text!=='')
				text+="\n";
		text+= " "+ this.state.Percentage+"% ";
		return text;
	}
	render() {
		this.props.self.skill=this.to_skill();
		return (
			<React.Fragment>
			<Card shadow={'always'} onClick={()=>{
				this.setState({	visible: true})
            }}>
				{this.state.header.split('\n').map(i=><p>{i}</p>)}
			</Card>
            <Drawer 
				visible={this.state.visible}
				className="test"
				onClose={()=>{this.setState({visible: false})}}
				close={false}
				width={320}
				height={320}
				placement={"right"}
				title={<p>{this.state.header}</p>}
				mask={true}
				maskClosable={true}
				zIndex={0}
				afterVisibleChange={()=> {}}
            >
				<Tooltip title="" placement={"Left"}>
						<div>
							<InputNumber defaultValue={this.state.Percentage} max={100} min={0} decimalPlaces={3} step={1} style={{width:'200px'}} 
							onChange={(Percentage)=>{
								if(isNaN(Percentage))
								Percentage=0;
								this.setState({Percentage},()=>{
									this.setState({header:this.header()});
								});
							} } 
						/>
						</div>
				</Tooltip>
				<Tooltip title="" placement={"Left"}>
						<div>
							<InputNumber defaultValue={this.state.HPGT} max={100} min={0} decimalPlaces={3} step={1} style={{width:'200px'}} 
							onChange={(HPGT)=>{
								if(isNaN(HPGT))
								HPGT=0;
								this.setState({HPGT},()=>{
									this.setState({header:this.header()});
								});
							} } 
						/>
						</div>
				</Tooltip>
				<Tooltip title="" placement={"Left"}>
						<div>
							<InputNumber defaultValue={this.state.AC} max={6} min={0} decimalPlaces={3} step={1} style={{width:'200px'}} 
							onChange={(AC)=>{
								if(isNaN(AC))
								AC=0;
								this.setState({AC},()=>{
									this.setState({header:this.header()});
								});
							} } 
						/>
						</div>
				</Tooltip>
				<Tooltip title="" placement={"Left"}>
						<div>
							<Checkbox.Group onChange={(AI)=>{
								this.setState({AI},()=>{
									this.setState({header:this.header()});
								});
							} } >
								<Checkbox></Checkbox>
								<Checkbox></Checkbox>
								<Checkbox></Checkbox>
								<Checkbox></Checkbox>
								<Checkbox></Checkbox>
								<Checkbox></Checkbox>
							</Checkbox.Group>
						</div>
				</Tooltip>
				
            </Drawer>
		</React.Fragment>
		)
	}
}
export default Dh;