import React from 'react';

//legao
import { Select,Drawer,Card,Tooltip } from '@feb-team/legao-react';
import '@feb-team/legao-react/dist/styles/css/legao.all.css';

class Dsv extends React.PureComponent{
	constructor(props) {
		super(props);
		this.state = {
			visible: false,
			header:'2 ',
			Count:2,
			Element:'',
		}
	}
	to_skill(){
		let Count=this.state.Count;
		let Element=this.state.Element;
		switch (Element) {
			case '':Element="*"; break;
			case '':Element="w"; break;
			case '':Element="f"; break;
			case '':Element="t"; break;
			case '':Element="l"; break;
			case '':Element="d"; break;
			case '':Element="h"; break;
			default:
		}
		return `dsv=${Count},${Element};`
	}
	header(){
		return `${this.state.Count} ${this.state.Element}`;
	}
	render() {
		this.props.self.skill=this.to_skill();
		return (
			<React.Fragment>
			<Card shadow={'always'} onClick={()=>{
				this.setState({	visible: true})
            }}>
				{this.state.header.split('\n').map(i=><p>{i}</p>)}
			</Card>
            <Drawer 
				visible={this.state.visible}
				className="test"
				onClose={()=>{this.setState({visible: false})}}
				close={false}
				width={320}
				height={320}
				placement={"right"}
				title={<p>{this.state.header}</p>}
				mask={true}
				maskClosable={true}
				zIndex={0}
				afterVisibleChange={()=> {}}
            >
				<Tooltip title="" placement={"Left"}>
						<div>
							<Select defaultValue={this.state.Count} clear={false} placeholder={""} 
							onChange={(Count)=>{
								this.setState({Count},()=>{
									this.setState({header:this.header()});
								});
							}}>
								<Select.Option value={2}>2</Select.Option>
								<Select.Option value={3}>3</Select.Option>
								<Select.Option value={4}>4</Select.Option>
							</Select>
						</div>
					</Tooltip>
					<Tooltip title="" placement={"Left"}>
						<div>
							<Select defaultValue={this.state.Element} clear={false} placeholder={""} 
							onChange={(Element)=>{
								this.setState({Element},()=>{
									this.setState({header:this.header()});
								});
							}}>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
								<Select.Option value={""}></Select.Option>
							</Select>
						</div>
					</Tooltip>
            </Drawer>
		</React.Fragment>
		)
	}
}
export default Dsv;