export function getMonthPreDay(year, month) {
    var date = new Date(year + '/' + month + '/01');
    var day = date.getDay();
    if (day == 0) {
        day = 7;
    }
    return day;
}
/**
 * 
 * @return {string}
 */
export function getNumTwoBit(n) {
    n = Number(n);
    return (n > 9 ? '' : '0') + n;
}
/**
 * 
 * @param year
 * @param month
 * @param day
 * @return {string}
 */
export function parseDate2Str(year, month, day) {
    return year + "-" + getNumTwoBit(month) + "-" + getNumTwoBit(day);
}
/**
 * 
 * @return {string}
 */
export function date2Str(date, split) {
    if (typeof date == 'string')
        return date;
    split = split || '-';
    var y = date.getFullYear();
    var m = getNumTwoBit(date.getMonth() + 1);
    var d = getNumTwoBit(date.getDate());
    return [y, m, d].join(split);
}
/**
 * 
 * @param {Number} 012
 * @return {string} '2014-12-31'
 */
export function getDay(i) {
    i = i || 0;
    var date = new Date();
    var diff = i * (1000 * 60 * 60 * 24);
    date = new Date(date.getTime() + diff);
    return date2Str(date);
}
